<template>
    <div page-index>
        <div>
            <CustomScoped1Vue />
            <CustomScoped2Vue />
        </div>
        <div v-card>
            <p>关于scoped属性</p>
            <div>
                <pre>
解决样式冲突问题
当多个组件内 同时声明同一个样式时 最先引入的组件中的css会被替换掉, 而使用scoped属性后, 每个组件内的样式互不影响
例:
  &lt;style scoped/&gt;
  ...
  &lt;style/&gt;
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import CustomScoped1Vue from '@/common/CustomScoped1.vue';
import CustomScoped2Vue from '@/common/CustomScoped2.vue';

export default {
    name: 'Scoped',
    components: { CustomScoped1Vue, CustomScoped2Vue },
    data() {
        return {};
    },
};
</script>

<style scoped></style>
